.loader__header {
  position: relative;
  left: -200px;
  h2 {
    display: inline;
    margin-left: 200px;
  }
}

.loader--horizontal {
  animation-duration: 2s;
  animation-name: slidein;
  height: 5px;
  background-color: $color-alizarin;
  position: absolute;
  right: 0;
  width: 300%;
}

@keyframes slidein {
  from {
    margin-left: 100%;
    width: 0%;
  }

  to {
    margin-left: 0%;
    width: 300%;
  }
}
