.feature-news {}

.feature-news__card {}

.feature-news__card--main {
	color: white;
	position: relative;

	.feature-news__card__body {
		position: absolute;
		bottom: 0;
		left: 30px;
	}

	img {
		width: 100%;
	}

	a {
		 @include anchor(skate--dark-bg);
	}
}

.feature-news__card--follow {
    box-shadow:  0 0 66px 3px rgba(0,0,0,0.15);
}

.feature-news__card--latest {}

.feature-news__card__body__title{

	&:after {
        content: "";
        display: block;
        width: 40px;
        height: 3px;
        background-color: $color-alizarin;
        margin: 20px auto;
	}
}

.feature-news__card__body__published {}
