.ksp-items {  
    display: flex;
    align-items: center;
    
}

.ksp__item {
	text-align: center;
	padding: $measure-medium 0;
	
}

.ksp__item__figure {
	font-size: 70px;
	color: $color-alizarin;
	font-weight: lighter;
	margin: 0;
	line-height: 1;
}

.ksp__item__pre-figure {
    color: $color-alizarin;
    text-transform: uppercase;
    margin: 0;
    font-weight: $font-weight-medium; 
}

.ksp__item__text {
	padding-top: 10px;
}

.ksp__item__border {
	border-right: 2px #f2f2f2 solid;
}
