.connect-social {
    text-align: center;
    margin-top: $grid-padding;
   
    display: flex;
    align-items: stretch;

    @include grid-column(4);

    & > div {
        box-shadow:  0 0 8px 3px rgba(50, 50, 50, 0.1);
        
        & > span {
            margin: $grid-padding;
        }
    }
}

.connect-social--twitter {
    background-color: $color-twitter-background;
    padding: $grid-padding;
    width: 100%;
    span {
        color: $color-twitter-blue;
        font-size: $font-size-delta;
    }
}


.connect-social--fb {
    background-color: $color-white;
    padding: $grid-padding;
    width: 100%;
    span {
        color: $color-facebook-blue;
    }
}

.connect-social--insta {
    background-color: transparent;
    width: 100%;
    img {
        width: 100%;
        height: auto;
    }
}

